<template>
  <div class="page">
    <div
      v-show="!clicked"
      class="button play"
      @click="clicked = true"
    ><span>Открыть послание</span></div>
    <yt-video
      v-if="clicked"
      videoId="8DUk10nV8D4"
      autoplay
      class="video"
      @ended="$router.push({name: 'code'})"
    />
  </div>
</template>

<script>
import YtVideo from '../components/Video';

export default {
  components: {
    YtVideo,
  },
  data() {
    return {
      clicked: false,
    };
  },
  methods: {
    runVideo() {
      this.clicked = true;
    },
  },
};
</script>

<style scoped>
.page {
  background-color: #000;
}
</style>



