<template>
  <div>
    <div class="code-fields">
      <input
        v-for="(digit, i)  in digits"
        :key="i"
        ref="digit"
        v-model="digits[i]"
        type="number"
        min="0"
        max="9"
        @input="onInput(i)"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    code: String,
  },
  data() {
    return {
      digits: [],
    };
  },
  computed: {
    inputCode() {
      return this.digits.join('');
    },
  },
  mounted() {
    const length = this.code.length;
    for (let i = 0; i < length; i += 1) {
      this.digits.push('');
    }
  },
  methods: {
    clear() {
      this.digits.forEach((d, i) => {
        this.digits[i] = '';
      });
      this.$refs.digit[0].focus();
      this.$emit('error');
    },
    onInput(i) {
      const d = this.digits[i];
      if (!d) return;
      if (i < this.code.length - 1) {
        this.$refs.digit[i + 1].focus();
        return;
      }
      if (this.inputCode !== `${this.code}`) {
        this.clear();
        return;
      }
      this.$emit('success');
    },
  },
};
</script>

<style scoped>
.code-fields input {
  display: inline-block;
  margin: 15px;
  line-height: 150px;
  width: 150px;
  font-size: 100px;
  text-align: center;
  outline: 0 !important;
  border-radius: 8px;
  border: 2px solid #333;
}
.code-fields input::-webkit-inner-spin-button {
  display: none !important;
}
</style>



