<template>
  <div class="page">
    <template v-if="!success">
      <div class="code-container">
        <div class="message">Как только верный код введёте,<br>Подсказку новую найдёте.</div>
        <Code
          code="8194"
          @error="error = true"
          @success="onSuccess"
        />
        <div
          v-if="error"
          class="message error"
        >Не угадали!</div>
      </div>
    </template>
    <template v-else>
      <yt-video
        v-if="!ended"
        videoId="0gj2l2a2XtU"
        autoplay
        class="story-video"
        @ended="ended = true"
      />
      <div
        v-else
        class="button"
        @click="ended = false"
      ><span>↻</span></div>
    </template>
  </div>
</template>

<script>
import Code from '../components/Code';
import YtVideo from '../components/Video';

export default {
  components: {
    Code,
    YtVideo,
  },
  data() {
    return {
      success: false,
      error: false,
      ended: false,
    };
  },
  methods: {
    onSuccess() {
      this.success = true;
    },
    refresh() {
      window.location.reload();
    },
  },
};
</script>

<style scoped>
.code-container {
  position: absolute;
  width: 100%;
  top: calc(50vh - 200px);
}

.message {
  font-size: 30px;
  line-height: 45px;
  padding: 15px 0;
}

.message.error {
  color: red;
}
</style>



