<template>
  <div class="page">
    <div
      ref="player"
      class="player"
    />
  </div>
</template>

<script>
import Player from 'youtube-player';

const ENDED = 0;

export default {
  props: {
    videoId: {
      type: String,
      default: '',
    },
    autoplay: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      player: null,
    };
  },
  mounted() {
    this.$nextTick(this.init);
  },
  methods: {
    init() {
      this.player = Player(this.$refs.player, {
        width: '100%',
        height: '100%',
        videoId: this.videoId,
        playerVars: {
          autoplay: this.autoplay ? 1 : 0,
          cc_load_policy: 0,
          controls: 0,
          iv_load_policy: 3,
          modestbranding: 1,
          rel: 0,
        },
      });
      this.player.setVolume(50);
      this.player.on('stateChange', this.onPlayerStateChange);
    },
    onPlayerStateChange(e) {
      if (e.data === ENDED) {
        this.$emit('ended');
      }
    },
  },
};
</script>



