import Vue from 'vue';
import Router from 'vue-router';

import index from '../pages/index';
import code from '../pages/code';

Vue.use(Router);

export default new Router({
  base: '/23_02_2019',
  mode: 'hash',
  routes: [
    {
      path: '/code',
      name: 'code',
      component: code,
    },
    {
      path: '/',
      name: 'index',
      component: index,
    },
  ],
});



